#home{
    background-image: url('../../Assets/home.avif');
    /* Source  https://unsplash.com/photos/m3m-lnR90uM */
    height: 100vh;
    background-size: cover;
    color: var(--white);
    align-items: flex-start;
    justify-content: center;
}

#home-content{
    display: flex;
    flex-direction: column;
    gap: 15px;
    text-transform: uppercase;
}

#home h1{
    color: var(--primary-color);
    font-size: 90px;
}

#home p{
    font-size: 24px;
    font-weight: 500;
}

#CTA{
    background-color: var(--white);
    width: 150px;
    padding: 10px;
    color: black;
    font-weight: bolder;
    text-align: center;
    transition: .4s;
}

#CTA:hover{
    color: var(--primary-color);
    transform: scale(1.08);
}